import React, { ChangeEvent, KeyboardEvent as ReactKeyboardEvent } from "react";
import styles from "./App.module.scss";
import { data } from "./list";

const utilizeFocus = () => {
  const ref: React.RefObject<HTMLInputElement> = React.createRef();
  const setFocus = () => {
    ref.current && ref.current.focus();
  };

  return { setFocus, ref };
};

export interface IConfig {
  startIndex: number;
  endIndex: number;
}

interface State {
  config: {
    startIndex: number;
    endIndex: number;
  };
  started: boolean;
  currentRowIndex: number;
  inverted: boolean;
  answer: string;
  wrongAnswer: boolean;
  finished: boolean;
  showInformationInput: boolean;
  information: string;
}
class App extends React.Component<{}, State> {
  private gameState: {
    [index: number]: { correct: boolean; incorrectCount: number };
  };

  private inputFocus;

  constructor(props: {}) {
    super(props);
    this.state = {
      config: {
        startIndex: 0,
        endIndex: 5,
      },
      started: false,
      currentRowIndex: 0,
      inverted: false,
      answer: "",
      wrongAnswer: false,
      finished: false,
      showInformationInput: false,
      information: "",
    };

    this.gameState = {};

    this.submitAnswer = this.submitAnswer.bind(this);
    this.startGame = this.startGame.bind(this);
    this.stopGame = this.stopGame.bind(this);
    this.nextWord = this.nextWord.bind(this);
    this.handleKeyUp = this.handleKeyUp.bind(this);
    this.changeInformation = this.changeInformation.bind(this);
    this.toggleInfoButton = this.toggleInfoButton.bind(this);

    this.inputFocus = utilizeFocus();
  }

  submitAnswer() {
    const { wrongAnswer, inverted, currentRowIndex, answer } = this.state;

    if (wrongAnswer) {
      this.nextWord();
      return;
    }

    const row = data[currentRowIndex];
    const rowState = this.gameState[currentRowIndex];

    let correct = false;
    if (inverted) {
      correct = row.word.toLowerCase().trim() === answer.toLowerCase().trim();
    } else {
      correct = row.translations.some(
        (translation) =>
          translation.trim().toLowerCase() === answer.trim().toLowerCase()
      );
    }
    if (correct) {
      rowState.incorrectCount = Math.max(rowState.incorrectCount - 1, 0);
      if (rowState.incorrectCount === 0) {
        rowState.correct = true;
      }
      this.nextWord();
    } else {
      rowState.correct = false;
      rowState.incorrectCount++;
      this.setState({ wrongAnswer: true });
    }
  }

  nextWord() {
    const { config, currentRowIndex } = this.state;

    const rangeLength = config.endIndex - config.startIndex;

    for (let i = 1; i < rangeLength; i++) {
      const nextIndex =
        ((currentRowIndex - config.startIndex + i) % rangeLength) +
        config.startIndex;
      if (!this.gameState[nextIndex].correct) {
        this.setState(
          {
            wrongAnswer: false,
            answer: "",
            currentRowIndex: nextIndex,
            information:
              window.localStorage.getItem(data[nextIndex].rank.toString()) ||
              "",
          },
          this.inputFocus.setFocus
        );
        return;
      }
    }

    this.setState({ finished: true });
  }

  handleKeyUp(e: KeyboardEvent) {
    if (e.key === "Enter") {
      this.submitAnswer();
    }
  }

  toggleInfoButton() {
    this.setState({
      showInformationInput: !this.state.showInformationInput,
    });
  }

  changeInformation(text: ChangeEvent<HTMLTextAreaElement>) {
    const info = text.target.value;
    window.localStorage.setItem(
      data[this.state.currentRowIndex].rank.toString(),
      info
    );
    this.setState({ information: info });
  }

  startGame() {
    const { config } = this.state;

    this.gameState = {};

    for (let i = config.startIndex; i <= config.endIndex; i++) {
      this.gameState[i] = {
        correct: false,
        incorrectCount: 0,
      };
    }
    this.setState({
      currentRowIndex: config.startIndex,
      wrongAnswer: false,
      started: true,
      finished: false,
      answer: "",
      showInformationInput: false,
      information:
        window.localStorage.getItem(data[config.startIndex].rank.toString()) ||
        "",
    });
    document.body.addEventListener("keyup", this.handleKeyUp);
  }

  stopGame() {
    this.setState({
      started: false,
      finished: false,
    });
    document.body.removeEventListener("keyup", this.handleKeyUp);
  }

  render() {
    const {
      finished,
      started,
      answer,
      config,
      currentRowIndex,
      wrongAnswer,
      showInformationInput,
      inverted,
    } = this.state;

    if (finished) {
      return (
        <div id={styles.mainContainer}>
          <p>Game finished!</p>
          <button className={styles.button} onClick={this.stopGame}>
            Play again
          </button>
        </div>
      );
    }

    if (started) {
      const currentRow = data[currentRowIndex];
      const link = currentRow.link;
      const word = currentRow.word;
      const wordWithLink = link ? (
        <a
          className={styles.wordLink}
          href={link}
          target={"_blank"}
          rel="noreferrer"
        >
          {word}
        </a>
      ) : (
        word
      );

      const currentWord = inverted
        ? currentRow.translations.join(", ")
        : wordWithLink;
      const correctAnswer = inverted
        ? currentRow.word
        : currentRow.translations.join(", ");

      return (
        <div id={styles.mainContainer}>
          <p>
            From rank {config.startIndex} to {config.endIndex}
          </p>
          <p>
            Current word:{" "}
            <span className={styles.currentWord}>{currentWord}</span>
          </p>
          <button
            onClick={this.toggleInfoButton}
            className={styles.informationButton}
          >
            i
          </button>
          <textarea
            className={`${styles.informationDisplay} ${
              !showInformationInput && styles.hiddenElement
            }`}
            onChange={this.changeInformation}
            value={this.state.information}
            onKeyUp={(e: ReactKeyboardEvent<HTMLTextAreaElement>) =>
              e.stopPropagation()
            }
          ></textarea>
          <input
            value={answer}
            autoFocus={true}
            spellCheck="false"
            className={styles.answerInput}
            onChange={(e) => this.setState({ answer: e.target.value })}
            disabled={wrongAnswer}
            ref={this.inputFocus.ref}
          />
          {wrongAnswer && <div>Correct answer: {correctAnswer}</div>}
          <button
            className={styles.button}
            onClick={this.submitAnswer}
            tabIndex={1}
          >
            {wrongAnswer ? "Next" : "Submit"}
          </button>
          <button className={styles.button} onClick={this.stopGame}>
            Stop
          </button>
        </div>
      );
    } else {
      return (
        <div id={styles.mainContainer}>
          <div className={styles.inputLabel}>Start</div>
          <input
            type="number"
            className={styles.rangeInput}
            value={config.startIndex}
            onChange={(e) =>
              this.setState({
                config: { ...config, startIndex: parseInt(e.target.value, 10) },
              })
            }
          />
          <div className={styles.inputLabel}>End</div>
          <input
            type="number"
            value={config.endIndex}
            className={styles.rangeInput}
            onChange={(e) =>
              this.setState({
                config: { ...config, endIndex: parseInt(e.target.value, 10) },
              })
            }
          />
          <div className={styles.invertedContainer}>
            <span>Inverted?</span>
            <input
              className={styles.selectInverted}
              type="checkbox"
              onChange={(e) => this.setState({ inverted: e.target.checked })}
              checked={inverted}
            />
          </div>
          <button className={styles.button} onClick={this.startGame}>
            Start
          </button>
        </div>
      );
    }
  }
}

export default App;
